import styles from "../product/product.module.css";

export default function ProductPage() {
  const [products, setProducts] = useState<Product[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchProducts = async () => {
      try {
        const res = await fetch("https://api-zeal.onrender.com/api/products");
        const data = await res.json();
        setProducts(data);
        setLoading(false);
      } catch (error) {
        console.error("Lỗi khi tải danh sách sản phẩm:", error);
        setLoading(false);
      }
    };

    fetchProducts();
  }, []);

  if (loading) {
    return <p className="text-center py-10">Đang tải danh sách sản phẩm...</p>;
  }

  return (
    <div>
      <div className={styles.title_container}>
        <h1>SẢN PHẨM</h1>
        <a href="/admin/add_product" className={styles.add_product_btn}>Thêm Sản Phẩm +</a>
      </div>

      <div className={styles.table_container}>
        <table>
          <thead>
            <tr>
              <th>Ảnh</th>
              <th>Tên sản phẩm</th>
              <th>Danh mục</th>
              <th>Giá</th>
              <th>Số lượng</th>
              <th>Hành động</th>
            </tr>
          </thead>
          <tbody>
            {products.map((product) => (
              <tr key={product._id}>
                <td>
                  <img src={`/images/${product.images[0]}`} alt={product.name} width="50" />
                </td>
                <td>{product.name}</td>
                <td>{product.category}</td>
                <td>{product.price.toLocaleString()}₫</td>
                <td>{product.quantity}</td>
                <td>
                  <button className={styles.edit_btn}>
                    <i className="fa-solid fa-pen-to-square"></i>
                  </button>
                  <button className={styles.delete_btn}>
                    <i className="fa-regular fa-trash-can"></i>
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      <div className={styles.pagination}>
        <a href="#" className={styles.page_link}>Trước</a>
        <a href="#" className={`${styles.page_link} ${styles.active}`}>1</a>
        <a href="#" className={styles.page_link}>2</a>
        <a href="#" className={styles.page_link}>3</a>
        <a href="#" className={styles.page_link}>Sau</a>
      </div>
    </div>
  );
}
